html, body {
  width: 100%;
  height: 100%;
  background-color: #f5f5f5;
  overflow: hidden;
  font-size: 16px;
  line-height: 20px;
}

.row {
  padding: 4px 16px;
}
.chat-row {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  
  overflow: hidden;
}

.row:first-child {
  padding-top: 16px;
}

.row:last-child {
  padding-bottom: 16px;
}

.bubble {
  max-width: 75%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 8px 12px;
  -webkit-transition: width .2s, height .2s;
  -o-transition: width .2s, height .2s;
  transition: width .2s, height .2s;
}

.shadow {
  height: 0;
  overflow: hidden;
}


.mock {
  visibility: hidden;
}

.left {
  float: left;
  background: #fff;
  border-radius: 20px 20px 20px 20px;
}

.right {
  float: right;
  color: #fff;
  border-radius: 20px 20px 20px 20px;
  background-color: #00709d;
}

.message-list {
  height: calc(100% - 82px);
}

.scrolly {
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}

.scrollx {
  overflow-y: hidden;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

#root {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.app {
  height: 100%;
  position: relative;
}

.quick-replies {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: #fff;
  height: 82px;
}

.quick-replies:before {
  content: " ";
  display: block;
  position: fixed;
  bottom: 82px;
  left: 0;
  width: 100%;
  height: 1px;
  -webkit-transform: scaleY(.5);
      -ms-transform: scaleY(.5);
          transform: scaleY(.5);
  background-color: #ddd;

}

.right-row {
  margin: 16px 0;
}

.replies {
  padding: 16px 16px 32px 16px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row no-wrap;
      flex-flow: row no-wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}



.replies button {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  display: block;
  margin-right: 8px;
  font-size: 14px;
  line-height: 18px;
  padding: 8px 16px;
  background-color: #ddd;
  border-radius: 25px;
  -webkit-appearance: none;
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
}

.blank {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 4px;
}

.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}

@-webkit-keyframes msgBounceIn {
  from, to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
              animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
  }

  from {
      -webkit-transform: scale(0);
              transform: scale(0);
  }

  40% {
      -webkit-transform: scale(1.05);
              transform: scale(1.05);
  }

  75% {
      -webkit-transform: scale(0.98);
              transform: scale(0.98);
  }

  to {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}

@keyframes msgBounceIn {
  from, to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
              animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
  }

  from {
      -webkit-transform: scale(0);
              transform: scale(0);
  }

  40% {
      -webkit-transform: scale(1.05);
              transform: scale(1.05);
  }

  75% {
      -webkit-transform: scale(0.98);
              transform: scale(0.98);
  }

  to {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}

.fade-enter {
  opacity: 0.01;
}

.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0.01;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}


.in-left .row {
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
}

.in-right .row {
  -webkit-transform-origin: 100% 100%;
      -ms-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
}

.example-enter.example-enter-active {
  
  -webkit-animation: msgBounceIn .5s;
  
          animation: msgBounceIn .5s;
}



.bottom-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 82px;
}

.inner {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #fff;
}

.replies {
  position: absolute;
  top: 0;
  left: 0;
}

.disabled {
  opacity: .3;
}

.wave {
  position:relative;
	text-align:center;
	width:40px;
  height:20px;
  bottom:-4px;
  margin-right: auto;
}


.dot {
  display:inline-block;
  width:10px;
  height:10px;
  border-radius:50%;
  margin-right:3px;
  background:#ddd;
  -webkit-animation: wave 1.3s linear infinite;
          animation: wave 1.3s linear infinite;
}

.dot:nth-child(2) {
  -webkit-animation-delay: -1.1s;
          animation-delay: -1.1s;
}

.dot:nth-child(3) {
  -webkit-animation-delay: -0.9s;
          animation-delay: -0.9s;
}
@-webkit-keyframes wave {
	0%, 60%, 100% {
		-webkit-transform: initial;
		        transform: initial;
	}

	30% {
		-webkit-transform: translateY(-12px);
		        transform: translateY(-12px);
	}
}
@keyframes wave {
	0%, 60%, 100% {
		-webkit-transform: initial;
		        transform: initial;
	}

	30% {
		-webkit-transform: translateY(-12px);
		        transform: translateY(-12px);
	}
}


